<template>
<li @click="v2m" v-bind:class="'table-view-cell item' + (active?' active':'')">
    <label v-bind:class="hasicon?'check-cion':''"><slot></slot></label>
</li>
</template>

<script>
//checkbox  radio , model
//
export default {
    name: 'check',
    props: {
        //model
        vkey: {
            required: true
        },
        hasicon: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            active: false
        }
    },
    created() {
        this.m2v();
    },
    methods: {
        m2v: function () {
            var val = this.vkey;
            this.active = this.ischeckbox ? (!!~this.$parent.model.indexOf(val)) : (this.$parent.model[0] === val);
        },
        v2m: function () {
            this.active = this.ischeckbox ? !this.active : true;
            this.getval();
        },
        getval: function () {
            var pmodel = this.$parent.model,
                val = this.vkey;
            if (this.ischeckbox) {
                if (this.active) {
                    pmodel.push(val)
                } else {
                    pmodel.splice(pmodel.indexOf(val), 1);
                }
            } else {
                this.$parent.$children.forEach(function (item) {
                    item.active = false;
                });
                pmodel.splice(pmodel.indexOf(val), 1);
                pmodel.push(val)
                this.m2v();
            }
        }
    },
    computed: {
        ischeckbox: function () {
            return this.$parent.type === 'checkbox';
        }
    }
}
</script>



