<template>
<modal :show.sync="innerShow" :type="type" :maskclose="maskclose" :mask="mask" :title="title" :hastitle="false">
    <slot>
        <div v-if="type==='loading' || type==='preload'" class="spinner">
            <div class="loading-layer">
                <div class="loading-img">
                    <div class="loading-cycle"></div>
                    <div class="loading-cycle-before"></div>
                </div>
                <p class="cp-h5-text">{{title}}</p>
            </div>
        </div>
    </slot>
</modal>
</template>

<script>
import modal from './modal.vue'
export default {
  components: {
        modal
    },
    props: {
        show: {
            type: Boolean,
            default: false
        },
        type: {
            type: String,
            default: 'loading'
        }, //loading preload
        //mask
        mask: {
            type: Boolean,
            default: true
        },
        //mask
        maskclose: {
            type: Boolean,
            default: true
        },
        title: {
            type: String,
            default: ""
        },
    },
    data(){
      return {
        innerShow: this.show
      }
    },
    watch: {
        innerShow(val) {
            // this.show = val
            this.$emit('update:show', val)
        },
        show(val) {
            this.innerShow = val
        }
    }
}
</script>

<style lang="sass">

@import './scss/phone';
$loadingSize: r(120px);
$loadingImgSize: r(72px);

.loading-enter-active,
.loading-leave-active {
    text-align: center;
}

.modal-loading,
.modal-preload {
    .modal-layout {
        overflow: hidden;
        width: $loadingSize;
        .modal-inner {
            background-color: transparent;
            width: $loadingSize;
            height: $loadingSize;
            text-align: center;
        }
        .modal-text {
            position: relative;
            padding: 0;
        }
    }
}

.modal-preload {
    position: static;
    background-color: transparent;
    transform: scale(.8);
}

/* loading layer */

.modal-loading {
    .modal-layout {
        height: $loadingSize;
    }
    .modal-inner {
        padding: 0;
        display: inline-block;
        background-color: #FFF !important;
        border-radius: r(13px);
    }
}

.loading-layer {
    z-index: 10000;
    color: #666;
    font-size: r(12px);
    text-align: center;
    width: 100%;
    height: 100%;
    .loading-img {
        width: $loadingSize*2/3;
        height: $loadingSize*2/3;
        margin: r(10px) auto r(2px);
        position: relative;
    }
    .loading-cycle,
    .loading-cycle-before {
        background-image: url("data:image/png;base64,") !important;
        background-size: r(135px) auto; // background-size: 200%;
        background-repeat: no-repeat;
        position: absolute;
    }
    .loading-cycle-before {
        content: "";
        background-position: r(-72px) 0; // background-position: 105% 0;
        position: absolute;
        top: r(17px);
        left: r(12px);
        width: $loadingImgSize;
        height: $loadingImgSize;
    }
    .loading-cycle {
        position: absolute;
        top: r(5px);
        left: r(5px);
        width: $loadingImgSize;
        height: $loadingImgSize;
        background-position: r(2px) r(2px);
        -webkit-animation: loading 1s linear 0s infinite;
        animation: loading 1s linear 0s infinite;
    }
    .cp-h5-text {
        width: $loadingSize;
        text-align: center;
        font-size: r(13px);
        /*     position: absolute;		    bottom: r(10px); */
    }
}

@-webkit-keyframes loading {
    0% {
        -webkit-transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
    }
}

@keyframes loading {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
</style>



